@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
html {
  height: 100%;
  margin: 0;
}

body {
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  margin: 0;
  background: url(/static/media/bg.c5d8014e.jpg) repeat-x;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  height: 100%;
  min-height: 100%;
}

.App {
  height: 100%;
  padding-left: 4%;
  padding-right: 4%;
}

#title {
  opacity: 1.0;
}

#subheader {
  opacity: 0.7;
  font-weight: 400;
}

#logo {
  width: 30%;
}

#songCard {
  margin-left: 8vw;
  width: 75%!important;
}

#songTitle {
  font-weight: 500;
}

.noSongsAlign {
  margin-top: -2em;
  padding-bottom: 2em;
}

.customAlert {
  padding: 0.5rem 1rem!important;
}

.alertDivider {
  margin: 0.5rem 0!important;
}

.alertButton {
  padding: 0.25rem 0.5rem;
  font-size: 0.6rem;
}

#artists {
  display: inline;
  font-weight: 400;
}

.playIcon {
  font-size: 1.25em;
  color: white;
}

.playIcon:hover,
.playIcon:focus,
.playIcon.active {
  cursor: pointer;
  color: #61cce7;
}

.spotifyIcon {
  font-size: 1.5em;
  color: white;
}

.spotifyIcon:hover,
.spotifyIcon:focus,
.spotifyIcon.active {
  cursor: pointer;
  color: #61cce7;
}

@media (max-width: 576px) { 
  #mainText {
    text-align: center;
  }
  .App {
    padding-top: 4%;
    padding-bottom: 4%;
  }
  #songCard {
    margin-left: 0;
    width: 100%!important
  }
}
.glider-contain {
  width: 100%;
  margin: 0 auto;
  position: relative;
}
.glider {
  margin: 0 auto;
  position: relative;
  overflow-y: hidden;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: none;
  transform: translateZ(0);
}
.glider-track {
  transform: translateZ(0);
  width: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  z-index: 1;
}
.glider.draggable {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: -webkit-grab;
  cursor: grab;
}
.glider.draggable .glider-slide img {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  pointer-events: none;
}
.glider.drag {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}
.glider-slide {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  justify-content: center;
  align-content: center;
  width: 100%;
  display: flex;
  align-items: center;
}
.glider-slide img {
  max-width: 100%;
}
.glider::-webkit-scrollbar {
  opacity: 0;
  height: 0;
}
.glider-prev,.glider-next {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  position: absolute;
  outline: none;
  background: none;
  padding: 0;
  z-index: 2;
  font-size: 60px;
  text-decoration: none;
  left: 20px;
  border: 0;
  top: 43%;
  cursor: pointer;
  color: rgb(255, 255, 255);
  outline: none!important;
  margin: 0 -0.25em;
  opacity: 1;
  line-height: 1;
  transition: opacity .5s cubic-bezier(.17,.67,.83,.67),
              color .5s cubic-bezier(.17,.67,.83,.67);
}
.glider-prev:hover,
.glider-next:hover,
.glider-prev:focus,
.glider-next:focus {
  color: #61cce7;
}
.glider-next {
  right: -23px;
  left: auto;
}
.glider-next.disabled,
.glider-prev.disabled {
  opacity: .25;
  color: #666;
  cursor: default;
}
.glider-slide {
  min-width: 150px;
}
.glider-hide {
  opacity: 0;
}
.glider-dots {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0.5em 0 auto 5vw;
  padding: 0;
}
.glider-dot {
  border: 0;
  padding: 0;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  outline: none;
  display: block;
  cursor: pointer;
  color: #ccc;
  border-radius: 999px;
  background: #ccc;
  width: 12px;
  height: 12px;
  margin: 7px;
}
.glider-dot:hover,
.glider-dot:focus,
.glider-dot.active {
  background: #61cce7;
}
@media(max-width: 36em){
  .glider::-webkit-scrollbar {
    opacity: 1;
    -webkit-appearance: none;
    width: 7px;
    height: 3px;
  }
  .glider::-webkit-scrollbar-thumb {
    opacity: 1;
    border-radius: 99px;
    background-color: rgba(156, 156, 156, 0.25);
    box-shadow: 0 0 1px rgba(255,255,255,.25);
  }
}

@media (max-width: 576px) { 
  .glider-prev {
    left: -23px;
    top: 36%;
  }
  .glider-next {
    top: 36%;
  }
  .glider-dots {
    margin-left: 0;
  }
}

